.TeamOverview-logo {
    height: 100%;
    position: absolute;
    left: 0;
    top: 0;
}

.TeamOverview-header {
    height: 4em;
    position: relative;
}

.TeamOverview-name {
    display: inline-block;
    font-size: 2rem;
    padding-bottom: 1em;
    padding-left: 2.5em;
    padding-top: 0.5em;
}

.TeamOverview-victory {
}

.TeamOverview-victory .opponentPoints{
    font-weight: lighter;
    color: grey;
}

.TeamOverview-victory .teamPoints {
    color:black;
    font-weight: bold;
}

.TeamOverview-draw {
    color:grey;
    font-weight: normal;
}

.TeamOverview-loss .opponentPoints {
    font-weight: bold;
    color: #000000;

}

.TeamOverview-loss .teamPoints {
    font-weight: normal;
    color: grey;
}

.TeamOverview-loss {
}

.TeamOverview a{
    color:#AAAAAA;
    font-size: 0.6em;
    padding: 2px;
    display: inline-block;
    text-decoration: none;
}


